}

h3 {
  font-family: "paralucent-stencil",sans-serif;
  font-style: normal;
  font-weight: 900;
  font-size: 3.6rem;
  color: #478E41;
  text-transform: uppercase;
}

h4 {
  font-family: "paralucent-stencil",sans-serif;
  font-style: normal;
