body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.cdr-navbar {
  background-position: top left;
  background-size: auto;
  background-repeat: repeat;
  background-image: url("../assets/img/texture.svg");
  position: relative;
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: space-between;
  padding: 1.1rem 1.8rem;
  z-index: 100;
}
@media (min-width: 768px) {
  .cdr-navbar {
    padding: 1.5rem 3rem;
    flex-flow: row nowrap;
    justify-content: flex-start;
  }
}
.cdr-navbar__brand {
  display: inline-block;
  padding-top: .3125rem;
  padding-bottom: .3125rem;
  margin-right: 1rem;
  font-size: 1.25rem;
  line-height: inherit;
  white-space: nowrap;
}
@media (min-width: 768px) {
  .cdr-navbar__brand #logoLight {
    display: none;
  }
}
.cdr-navbar .cdr-menu {
  flex: 1 1 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  padding-left: 0;
  margin-bottom: 0;
  list-style: none;
}
@media (min-width: 768px) {
  .cdr-navbar .cdr-menu {
    flex-direction: row;
  }
}
@media (min-width: 768px) {
  .cdr-navbar .cdr-menu .menu-item-has-children {
    position: relative;
  }
  .cdr-navbar .cdr-menu .menu-item-has-children .sub-menu {
    position: absolute;
    top: 100%;
    left: -25%;
    list-style: none;
    padding-left: 0;
    z-index: -15;
    display: none;
    flex-flow: row wrap;
    width: 150%;
  }
  .cdr-navbar .cdr-menu .menu-item-has-children .sub-menu.show {
    display: block;
  }
  .cdr-navbar .cdr-menu .menu-item-has-children .sub-menu li {
    background: white;
    background-position: top left;
    background-size: auto;
    background-repeat: repeat;
    background-image: url("../assets/img/texture.svg");
    flex: 0 0 100%;
    transition: all .15s;
    position: relative;
    perspective: 100px;
    transform: rotateX(145deg);
    opacity: 0;
    transform-origin: 0 top;
  }
  .cdr-navbar .cdr-menu .menu-item-has-children .sub-menu li.flip {
    transform: rotateX(0);
    opacity: 1;
  }
}
.cdr-navbar .menu-item a {
  font-family: "paralucent-stencil",sans-serif;
  font-style: normal;
  font-weight: 900;
  font-size: 1.8rem;
  text-transform: uppercase;
  display: block;
  padding: .5rem 1rem;
}

.support-wrapper {
  flex: 1 0 110px;
  width: 110px;
  height: 35px;
  position: relative;
}

.support-button {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.support-button svg {
  position: absolute;
  max-width: 100%;
  max-height: 100%;
  top: 0;
  left: 0;
}
.support-button span {
  position: absolute;
  left: 0;
  top: 0;
  margin-left: 19px;
  line-height: 34px;
  font-family: "paralucent-stencil",sans-serif;
  font-style: normal;
  font-weight: 900;
  color: #478E41;
  font-size: 1.8rem;
  text-transform: uppercase;
}

.cdr-navbar__toggler {
  display: block;
  background: transparent;
  border: 0px;
  height: 41px;
  width: 41px;
  padding: 0;
  transition: all .25s;
  z-index: 10;
}
.cdr-navbar__toggler * {
  transition: all .25s;
}
.cdr-navbar__toggler span {
  position: absolute;
  display: block;
  opacity: 1;
  right: 28px;
  width: 20px;
  height: 2px;
  background: #478E41;
  transform: rotate(0deg);
}
.cdr-navbar__toggler span:nth-child(2) {
  top: 24px;
}
.cdr-navbar__toggler span:nth-child(3) {
  top: 31px;
}
.cdr-navbar__toggler span:nth-child(4) {
  top: 38px;
}
.cdr-navbar__toggler.open svg {
  transform: rotateZ(360deg);
}
.cdr-navbar__toggler.open span {
  background: white;
}
.cdr-navbar__toggler.open span:nth-child(2) {
  top: 31px;
  transform: rotate(-135deg);
}
.cdr-navbar__toggler.open span:nth-child(3) {
  opacity: 0;
}
.cdr-navbar__toggler.open span:nth-child(4) {
  top: 31px;
  transform: rotate(135deg);
}
@media (min-width: 768px) {
  .cdr-navbar__toggler {
    display: none;
  }
}
.cdr-navbar__toggler:focus {
  outline: none;
}
@media (max-width: 767px) {
  .cdr-navbar__brand {
    position: relative;
    z-index: 10;
    width: 300px;
  }
  .cdr-navbar__brand img {
    transition: all .25s;
    position: absolute;
    top: 0;
    left: 0;
  }
}
.cdr-navbar__nav {
  flex: 1 auto;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  text-align: center;
}
@media (max-width: 767px) {
  .cdr-navbar__nav {
    transform: translateY(-100%);
    transition: all .3s ease-in-out;
    flex-flow: column;
    align-items: center;
    justify-content: flex-start;
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    padding-top: 70px;
    background-color: #6F7832;
    background-position: top left;
    background-size: auto;
    background-repeat: repeat;
    background-image: url("../assets/img/texture.svg");
  }
  .admin-bar .cdr-navbar__nav {
    height: calc(100vh - 46px);
  }
  .cdr-navbar__nav * {
    color: white;
  }
  .cdr-navbar__nav .support-wrapper {
    margin-top: 3rem;
    flex: 0 0 60px;
    width: 180px;
    height: 60px;
    order: 1;
    position: relative;
  }
  .cdr-navbar__nav .support-wrapper a, .cdr-navbar__nav .support-wrapper svg {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
  }
  .cdr-navbar__nav .support-wrapper span {
    left: 0;
    top: 0;
    width: 100%;
    text-align: center;
    font-size: 2.8rem;
    line-height: 58px;
    margin: 0;
    color: white;
  }
  .cdr-navbar__nav .cdr-menu {
    order: 2;
    flex: 0 auto;
    margin-top: 2rem;
  }
  .cdr-navbar__nav .cdr-menu .menu-item {
    flex: 0 0 100%;
  }
  .cdr-navbar__nav .cdr-menu .menu-item a {
    font-size: 3rem;
    padding: 1.6rem 0;
  }
  .cdr-navbar__nav .cdr-menu .menu-item a:hover {
    color: white;
    text-decoration: none;
  }
  .cdr-navbar__nav .cdr-menu .menu-item a:focus {
    color: white;
    text-decoration: none;
  }
  .cdr-navbar__nav .cdr-menu .menu-item a i {
    transition: all .25s;
  }
  .cdr-navbar__nav .cdr-menu .menu-item-has-children .sub-menu {
    list-style: none;
    padding-left: 0;
    transition: all .25s;
  }
  .cdr-navbar__nav .cdr-menu .menu-item-has-children .sub-menu a {
    color: #97AA3A;
  }
}

